import React, {useEffect} from 'react';
import {useHistory} from "react-router-dom";
import {useAppSelector} from "../../hooks/redux";
import queryString from "query-string";

const CallPage = () => {

  const history = useHistory();

  // Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ¾Ð² Ð¿Ð¾Ð¸ÑÐºÐ°
  const searchParams = useAppSelector(state => state.calls.callPageSearchParams);
  useEffect(() => {
    const searchParamsObject = queryString.parse(searchParams);
    if (searchParamsObject.id && searchParamsObject.token) {
      history.push(searchParams);

    }
  }, [searchParams]);

  return (
    <div>
      <h1>
        Call Page
      </h1>
    </div>
  );
};

export default CallPage;